<template>
<div>
  <!--
  <div class="field is-grouped is-grouped-right">
    <div class="control" :class="loading ? 'has-icons-left' : ''">
      <span class="select">
        <select class="select" v-model="leagueSeason" @change="updateTable">
          <option value="">Season</option>
          <option v-for="season in seasons" :key="season" v-text="season" :value="season"></option>
        </select>
      </span>
      <span v-if="loading" class="icon is-small is-left">
        <i class="fas fa-spinner" aria-hidden="true"></i>
      </span>
    </div>
  </div>
  -->
  <div>
    <div class="card" v-for="competition in competitions" :key="competition.id">
      <div class="card-content">
        <div class="media">
          <div class="media-content">
            <router-link :to="{ name: 'LeagueTable', params: { 'competition': competition.id }}">
              <p class="title is-4" v-text="competition.caption"></p>
            </router-link>
            <p class="subtitle is-6">
              Number of teams: <span v-text="competition.numberOfTeams"></span>
            </p>
          </div>
        </div>
        <div class="has-text-right">
          <small>
            Updated
            <time :title="competition.lastUpdated" :datetime="competition.lastUpdated">
              {{ competition.lastUpdated | fromNow }}
            </time>
          </small>
        </div>
        <div class="content">
        </div>
      </div>
    </div>
  </div>
  <br>
</div>
</template>

<script>
import { mapGetters } from 'vuex';
import DateParsers from '@/mixins/DateParsers';

export default {
  name: 'CompetitionsTable',
  mixins: [DateParsers],
  data() {
    return {
      leagueSeason: '',
    };
  },
  computed: {
    loading() {
      return this.$store.getters.isLoading('competitions');
    },
    seasons() {
      const currentYear = new Date().getFullYear() - 1;
      return Array.from(new Array(10), (x,i) => currentYear - i);
    },
    ...mapGetters({
      competitions: 'getLeagues',
    }),
  },
  methods: {
    updateTable() {
      this.$store.dispatch('fetchCompetitions', { season: this.leagueSeason });
    }
  },
};
</script>



