const isLoading = ({ loading }) => type => loading.includes(type);
const getLeagueTable = ({ league }) => league.table;
const getLeagueTeams = ({ league }) => league.teams;
const getLeagueFixtures = ({ league }) => league.fixtures;
const getLeagueFixturesSorted = ({ league }) => () => league.fixtures;
const getLeagueFixturesByStatus = state => (status = 'ALL') => (status === 'ALL') ? state.league.fixtures : state.league.fixtures.filter(fixture => fixture.status === status.toUpperCase());
const getLeagues = ({ leagues }) => leagues;
const getTeam = ({ team }) => team;
const getTeamPlayers = ({ players }) => players;

export default {
  isLoading,
  getLeagueTable,
  getLeagueTeams,
  getLeagueFixtures,
  getLeagueFixturesSorted,
  getLeagueFixturesByStatus,
  getLeagues,
  getTeam,
  getTeamPlayers,
};



