import api from '@/api';
import * as types from './mutation-types';
import * as LOAD from './loading-types';

const startLoading = type => ({ type, status: true });
const stopLoading = type => ({ type, status: false });

const fetchCompetitions = ({ commit }, params) => {
  commit(types.LOADING, startLoading(LOAD.COMPETITIONS));

  api.getLeagues(params)
    .then(({ data }) => {
      commit(types.COMPETITIONS, data);
      commit(types.LOADING, stopLoading(LOAD.COMPETITIONS));
    })
    .catch((error) => {
      commit(types.LOADING, stopLoading(LOAD.COMPETITIONS));
      throw (error);
    });
};

const fetchLeague = function fetchLeague({ commit }, { id, params }) {
  commit(types.LEAGUE_TABLE, {}); // Clear state before fetching new

  commit(types.LOADING, startLoading(LOAD.LEAGUE_TABLE));

  api.getLeagueTable(id, params)
    .then(({ data }) => {
      commit(types.LEAGUE_TABLE, data);
      commit(types.LOADING, stopLoading(LOAD.LEAGUE_TABLE));
    })
    .catch((error) => {
      commit(types.LOADING, stopLoading(LOAD.LEAGUE_TABLE));
      throw (error);
    });
};

const fetchLeagueWithTeams = function fetchLeague({ commit }, { id, params }) {
  commit(types.LEAGUE_TABLE, {}); // Clear state before fetching new
  commit(types.LEAGUE_TEAMS, {}); // Clear state before fetching new

  commit(types.LOADING, startLoading(LOAD.LEAGUE_TABLE));
  commit(types.LOADING, startLoading(LOAD.LEAGUE_TEAMS));

  api.getLeagueTableWithTeams(id, params)
    .then(({ table, teams }) => {
      // Update standings with team's crest
      const teamsMap = new Map();

      teams.forEach((team) => {
        teamsMap.set(team.name, team);
      });

      table.standing.forEach((standing) => {
        standing.team = teamsMap.get(standing.teamName);
      });

      commit(types.LEAGUE_TABLE, table);
      commit(types.LEAGUE_TEAMS, teams);
      commit(types.LOADING, stopLoading(LOAD.LEAGUE_TABLE));
      commit(types.LOADING, stopLoading(LOAD.LEAGUE_TEAMS));
    })
    .catch((error) => {
      commit(types.LOADING, stopLoading(LOAD.LEAGUE_TABLE));
      commit(types.LOADING, stopLoading(LOAD.LEAGUE_TEAMS));
      throw (error);
    });
};

